import React from 'react'
import './HomeCard.css'
import home6 from '../../assets/home6.jpg'
import home4 from '../../assets/home4.jpg'
import home7 from '../../assets/home7.jpg'
import { Link } from 'react-router-dom'

function HomeCard() {
  return (
    <div className="container d-flex align-items-center justify-content-center flex-wrap">
        <div className="box">
            <div className="body">
                <div className="imgContainer">
                    <img src={home7}  alt=""/>
                </div>
                <div className="content d-flex flex-column align-items-center justify-content-center">
                    <div>
                        <h3 className="text-white fs-5" style={{fontFamily:'Comforter',color:'wheat',fontSize:'30px'}}>Select From The Best</h3>
                        <p className="p  fs-6 text-white">Stylish Pendant Store</p>
                        <button className='btn ' style={{paddingLeft:'30px'}} ><Link to='/pendant' style={{textDecoration:'none',color:'black'}}>shop now</Link></button>
                    </div>
                </div>
            </div>
        </div>
        <div className="box">
            <div className="body">
                <div className="imgContainer">
                    <img src={home4} alt="" />
                </div>
                <div className="content d-flex flex-column align-items-center justify-content-center">
                    <div>
                        <h3 className="text-white fs-5" style={{fontFamily:'Homemade Apple',color:'wheat',fontSize:'20px'}}>Trending Products</h3>
                        <p className="p fs-6 text-white">Classic Jwellery Collections</p>
                        <button className='btn ' style={{paddingLeft:'30px'}}><Link to='/earrings' style={{textDecoration:'none',color:'black'}}>shop now</Link></button>
                    </div>
                </div>
            </div>
        </div>
        <div className="box">
            <div className="body">
                <div className="imgContainer">
                    <img src={home6} alt=""/>
                </div>
                <div className="content d-flex flex-column align-items-center justify-content-center">
                    <div>
                        <h3 className="text-white fs-5" style={{fontFamily:'Explora',fontSize:'35px',color:'wheat',fontWeight:'600'}}>Never Go Out of Style</h3>
                        <p className="p fs-6 text-white">Top Ring Collections</p>
                        <button className='btn' style={{paddingLeft:'30px'}}><Link to='/rings' style={{textDecoration:'none',color:'black'}}>shop now</Link></button>
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}

export default HomeCard