import React from 'react'
import Header from './Header'
import Footer from './Footer/Footer'
import logo from '../assets/logo192.jpg'

function About() {
  return (
    <>
    <Header/>
    <div class="container  mt-5">
    <div class="hero">
    <div class="px-4 py-5 my-5 text-center">
    <img src={logo} style={{width:'55px'}}  />
        <h1 class="display-5 fw-bold">About us</h1>
        <div class="col-lg-6 mx-auto">
            <p class="lead mb-4">Jwel Group was founded in 1993 by a team of enterprising entrepreneurs, led by the visionary genius of Mrs.K FARZEENA when he explored beyond his immediate circle in the agri-corp industry to establish a jewellery trading company . Since then, keeping alive the golden spirit of the brand, the Group has remained constant, in its successful forays nurtured by expertise, vision and guidance, into a formidable business conglomerate.</p>
            <div class="d-grid gap-2 d-sm-flex justify-content-sm-center">
               
                
            </div>
        </div>
    </div>
    </div>
</div>
<Footer/>
    </>
  )
}

export default About